.login{
    z-index: 1050; /* or higher, if needed */
    margin-top: 5rem;
  }
.modal-backdrop{
    z-index: 0;
}


/* Set the initial state of the element */
.ping {
  font-size: 1rem;
  margin: 1rem 1rem;
  animation: color-toggle 2s infinite;
}

/* Define the keyframe animation */
@keyframes color-toggle {
  0% {
    color: red;
  }
  50% {
    color: green;
  }
  100% {
    color: red;
  }
}
