@import url('https://fonts.googleapis.com/css2?family=Italianno&family=Roboto+Serif:wght@500&display=swap');

body{
    background-color: rgb(15, 15, 15);
    color: rgb(251, 253, 255);
}

.home{
    padding-top: 8rem;
    font-family: 'Chivo Mono', monospace;
}
 
@keyframes rotate {
    0% {
        transform: rotateY(0deg);
    }
    100% {
        transform: rotateY(360deg);
    }
}

.coin{
    border-radius: 20rem;
    width: 25rem;
    box-shadow: 0 0 20px rgba(255, 255, 255, 0.7);
    animation: glow 1.5s ease-in-out infinite alternate ,rotate 5s infinite linear;
}

@keyframes glow {
    from {
      box-shadow: 0 0 60px #0275d8;
    }
    to {
      box-shadow: 0 0 60px rgba(255, 255, 255, 0.9);
    }
  }


@media screen and (max-width :656px){
    .home{
        padding-top: 4rem;
    }
    .coin{
        width: 15rem;
    }
}

.CARD:hover{
    transform: scale(1.2);
    transition: 0.7s ease-in-out;
}